import { useState, useEffect, useContext } from 'react';
import { FiPlus } from 'react-icons/fi';
import Header from '../../components/Header';
import Title from '../../components/Title';
import { AuthContext } from '../../contexts/auth'
import firebase from '../../services/firebaseConnection'
import { useHistory, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import './new.css';

const New = () => {

    const { id } = useParams();
    const history = useHistory();

    const [loadCustomers, setLoadCustomers] = useState(true)
    const [customers, setCustomers] = useState([])
    const [customerSelected, setCustomerSelected] = useState(0)

    const [assunto, setAssunto] = useState('Suporte');
    const [status, setStatus] = useState('Aberto')
    const [complemento, setComplemento] = useState('')

    const [idCustomer, setIdCustomer] = useState(false);

    const { user } = useContext(AuthContext);

    useEffect(() => {
        const loadCustomers = async () => {
            await firebase.firestore().collection('customers')
                .get()
                .then((snapshot) => {
                    let lista = [];

                    snapshot.forEach((doc) => {
                        lista.push({
                            id: doc.id,
                            nameFantasia: doc.data().nameFantasia
                        })
                    })
                    if (lista.length === 0) {
                        console.log('NENHUMA EMPRESA ENCONTRADA');
                        setCustomers([{ id: '1', nameFantasia: 'FREELA' }]);
                        setLoadCustomers(false);
                        return;
                    }

                    setCustomers(lista);
                    setLoadCustomers(false);

                    if (id) {
                        loadId(lista);
                    }
                })
                .catch((error) => {
                    console.log('DEU ALGUM ERRO', error)
                    setLoadCustomers(false);
                    setCustomers([{ id: '1', nameFantasia: '' }]);
                })
        }

        const loadId = async (lista) => {
            await firebase.firestore().collection('tickets').doc(id)
                .get()
                .then((snapshot) => {
                    setAssunto(snapshot.data().assunto);
                    setStatus(snapshot.data().status);
                    setComplemento(snapshot.data().complemento);

                    let index = lista.findIndex(item => item.id === snapshot.data().clienteId)
                    setCustomerSelected(index);
                    setIdCustomer(true)
                })
                .catch((error) => {
                    console.log('ERRO NO ID PASSADO: ', error)
                    setIdCustomer(false)
                })
        }

        loadCustomers();
    }, [id])



    const handleRegister = async (e) => {
        e.preventDefault();

        if (idCustomer) {
            await firebase.firestore().collection('tickets')
                .doc(id)
                .update({
                    cliente: customers[customerSelected].nameFantasia,
                    clienteId: customers[customerSelected].id,
                    assunto: assunto,
                    status: status,
                    complemento: complemento,
                    userId: user.uid
                })
                .then(() => {
                    toast.success('Chamado editado!')
                    setCustomerSelected(0);
                    setComplemento('');
                    history.push('/dashboard')
                })
                .catch((error) => {
                    toast.error('Erro ao editar.')
                    console.log(error)
                })

            return;
        }

        await firebase.firestore().collection('tickets')
            .add({
                created: new Date(),
                cliente: customers[customerSelected].nameFantasia,
                clienteId: customers[customerSelected].id,
                assunto: assunto,
                status: status,
                complemento: complemento,
                userId: user.uid
            })
            .then(() => {
                toast.success('Chamado criado com sucesso');
                setComplemento('');
                setCustomerSelected(0);
            })
            .catch((error) => {
                toast.error('Não deu pra registrar, tente mais tarde.')
                console.log(error)
            })
    }

    //Chamado quando troca o assunto
    const handleChangeSelect = (e) => {
        setAssunto(e.target.value)
    }

    //Chamado quando troca o status
    const handleOptionChange = (e) => {
        setStatus(e.target.value)
        console.log(e.target.value)
    }

    // Chamado quando troca de cliente
    const handleChangeCustomers = (e) => {
        //console.log('INDEX DO CLIENTE SELECIONADO', e.target.value);
        //console.log('Cliente selecionado', customers[e.target.value]);
        setCustomerSelected(e.target.value)

    }

    return (
        <>
            <Header />
            <div className="content">
                <Title name="Novo chamado">
                    <FiPlus size={25} />
                </Title>

                <div className="container">
                    <form className="form-profile" onSubmit={handleRegister}>
                        <label>Cliente</label>

                        {loadCustomers ? (
                            <input type="text" disabled={true} value="Carregando clientes..." />
                        ) : (
                            <select value={customerSelected} onChange={handleChangeCustomers}>
                                {customers.map((item, index) => {
                                    return (
                                        <option key={item.id} value={index}>
                                            {item.nameFantasia}
                                        </option>
                                    )
                                })}
                            </select>
                        )}

                        <label>Assunto</label>
                        <select value={assunto} onChange={handleChangeSelect}>
                            <option value="Suporte">Suporte</option>
                            <option value="Visita Técnica">Visita Técnica</option>
                            <option value="Financeiro">Financeiro</option>
                        </select>

                        <label>Status</label>
                        <div className="status">
                            <input
                                type="radio"
                                name="radio"
                                value="Aberto"
                                onChange={handleOptionChange}
                                checked={status === 'Aberto'}
                            />
                            <span>Em aberto</span>
                            <input
                                type="radio"
                                name="radio"
                                value="Progresso"
                                onChange={handleOptionChange}
                                checked={status === 'Progresso'}
                            />
                            <span>Progresso</span>
                            <input
                                type="radio"
                                name="radio"
                                value="Atendido"
                                onChange={handleOptionChange}
                                checked={status === 'Atendido'}
                            />
                            <span>Atendido</span>

                        </div>

                        <label>Complemento</label>
                        <textarea
                            type="text"
                            placeholder="Descreva seu problema (opcional)"
                            value={complemento}
                            onChange={(e) => setComplemento(e.target.value)}
                        />

                        <button type="submit">Registrar</button>
                    </form>
                </div>
            </div>

        </>
    )
}

export default New
