import { useState, useEffect } from 'react';
import './dashboard.css';
import Header from '../../components/Header';
import Title from '../../components/Title';
import Modal from '../../components/Modal';
import { FiEdit2, FiMessageSquare, FiPlus, FiSearch } from 'react-icons/fi';
import { Link } from 'react-router-dom';
import firebase from '../../services/firebaseConnection';
import { format } from 'date-fns';

const listRef = firebase.firestore().collection('tickets').orderBy('created', 'desc')

const Dashboard = () => {

    const [chamados, setChamados] = useState([]);
    const [loading, setLoading] = useState(true);
    const [loadingMore, setLoadingMore] = useState(false);
    const [isEmpty, setIsEmpty] = useState(false);
    const [lastDocs, setLastDocs] = useState();

    const [showPostModal, setShowPostModal] = useState(false);
    const [detail, setDetail] = useState();

    useEffect(() => {

        const loadChamados = async () => {
            await listRef.limit(5)
                .get()
                .then((snapshot) => {
                    updateState(snapshot)
                })
                .catch((error) => {
                    console.log('Deu algum erro: ', error)
                    setLoadingMore(false)
                })

            setLoading(false)
        }

        loadChamados()

        return () => {

        }
    }, [])


    const updateState = async (snapshot) => {
        const isCollectionEmpty = snapshot.size === 0;

        if (!isCollectionEmpty) {
            let list = [];

            snapshot.forEach((doc) => {
                list.push({
                    id: doc.id,
                    assunto: doc.data().assunto,
                    cliente: doc.data().cliente,
                    clienteId: doc.data().clienteId,
                    created: doc.data().created,
                    createdFormated: format(doc.data().created.toDate(), 'dd/MM/yyyy'),
                    status: doc.data().status,
                    complemento: doc.data().complemento
                })
            })
            const lastDoc = snapshot.docs[snapshot.docs.length - 1] //Pegando o último documento buscado

            setChamados(chamados => [...chamados, ...list]);
            setLastDocs(lastDoc);
        } else {
            setIsEmpty(true)
        }

        setLoadingMore(false)
    }

    const handleMore = async () => {
        setLoadingMore(true)
        await listRef.startAfter(lastDocs).limit(5)
            .get()
            .then((snapshot) => {
                updateState(snapshot)
            })
    }

    const togglePostModal = (item) => {
        setShowPostModal(!showPostModal)
        setDetail(item)
    }

    if (loading) {
        return (
            <div>
                <Header />
                <div className="content">
                    <Title name="Atendimentos">
                        <FiMessageSquare size={25} />
                    </Title>

                    <div className="container dashboard">
                        <span>Buscando chamados...</span>
                    </div>
                </div>
            </div>
        )
    }

    return (
        <>
            <Header />
            <div className="content">
                <Title name="Atendimentos">
                    <FiMessageSquare size={25} />
                </Title>

                {chamados.length === 0 ? (
                    <div className="container dashboard">
                        <span>Nenhum chamado registrado...</span>
                        <Link to="/new" className="new">
                            <FiPlus size={25} color='#FFF' />
                            Novo chamado
                        </Link>
                    </div>
                ) : (
                    <>
                        <Link to="/new" className="new">
                            <FiPlus size={25} color='#FFF' />
                            Novo chamado
                        </Link>

                        <table>
                            <thead>
                                <tr>
                                    <th scope="col">Cliente</th>
                                    <th scope="col">Assunto</th>
                                    <th scope="col">Status</th>
                                    <th scope="col">Cadastrado em</th>
                                    <th scope="col">#</th>
                                </tr>
                            </thead>
                            <tbody>
                                {chamados.map((item, index) => {
                                    return (
                                        <tr key={index}>
                                            <td data-label="Cliente">{item.cliente}</td>
                                            <td data-label="Assunto">{item.assunto}</td>
                                            <td data-label="Status">
                                                <span className="badge" style={{ backgroundColor: item.status === 'Aberto' ? '#5cb85c' : '#999' }}>{item.status}</span>
                                            </td>
                                            <td data-label="Cadastrado">{item.createdFormated}</td>
                                            <td data-label="#">
                                                <button className="action" style={{ backgroundColor: '#3583f6' }} onClick={() => togglePostModal(item)}>
                                                    <FiSearch color="FFF" size={17} />
                                                </button>
                                                <Link to={`/new/${item.id}`} className="action" style={{ backgroundColor: '#f6a935' }}>
                                                    <FiEdit2 color="FFF" size={17} />
                                                </Link>
                                            </td>
                                        </tr>
                                    )
                                })}

                            </tbody>
                        </table>
                        {loadingMore && <h3 style={{ textAlign: 'center', marginTop: 15 }}>Buscando chamados... </h3>}
                        {!loadingMore && !isEmpty && <button className="btn-more" onClick={handleMore}>Buscar mais</button>}
                    </>
                )}
            </div>
            {showPostModal && <Modal 
                conteudo={detail}
                close={togglePostModal}
            />}
        </>
    )
}

export default Dashboard
